/* eslint-disable no-array-constructor */
import styles from "./Project.module.css";
import Projects from "../components/projects";
import friendImg from "../img/project/friend.png"
import VVRImg from "../img/project/VVR.png"
import osolemioImg from "../img/project/osolemio.png"
