/* eslint-disable no-array-constructor */
import styles from "./Profile.module.css";
import Info from "../components/info";
import Skill from "../components/skill";

import personImg from "../img/profile/person.png"
import calendarImg from "../img/profile/calendar.png"
import phoneImg from "../img/profile/phone.png"
import emailImg from "../img/profile/email.png"

function Profile(){
    //        
    return(
        <div className={styles.profile}>
            <div className={styles.left}>
                <div className={styles.profileTitle}>
                    Who I am
                </div>
                <div className={styles.comment}>
                         .
                </div>
                <div className={styles.info}>
                    <Info 
                        img={personImg}
                        info={"Name"}
                        content={""}
                    />
                    <Info 
                        img={calendarImg}
                        info={"Birth"}
                        content={"01.07.28"}
                    />
                    <Info 
                        img={phoneImg}
                        info={"Phone number"}
                        content={"010-7280-0134"}
                    />
                    <Info 
                        img={emailImg}
                        info={"Contact"}
                        content={"cnh0728@gmail.com"}
                    />
                </div>
            </div>
            <div className={styles.skill}>
                <div className={styles.language}>
                    <Skill 
                        title={"Language"}
                        content={
                            Array(
                                "C",
                                "C++",
                                "C#",
                                "Python",
                                "Java",
                                "JavaScript",
                                "HTML",
                                "CSS",
                                "MYSQL",
                                "Solidity"
                        )}
                    />
                </div>
                <div className={styles.framework}>
                    <Skill 
                        title={"FrameWork"}
                        content={
                            Array(
                                "Unity",
                                "React",
                                "TensorFlow",
                                "RaspberryPi",
                                "Node.js",
                                "OpenCV",
                                "MCU",
                                "MFC"
                        )}
                    />
                </div>
                <div>
                    <div className={styles.Awards}>
                        <Skill
                            title={"Awards"}
                            content={
                                Array(
                                    "2018  - ",
                                    "2018  - "
                                )}
                        />
                    </div>
                    <div className={styles.certificate}>
                        <Skill
                            title={"Certificate"}
                            content={
                                Array(
                                    " / 19.07",
                                    "1/ 21.02",
                                    " / 22.07"
                                )}
                        />
                    </div>

                </div>
            </div>
        </div>
    );
}

export default Profile;